import React, {Component, Fragment} from 'react';

import '../style/pop-up.scss'


export  default function PopUp(props) {
    return (
        <Fragment>
            <div className={props.className}>
                <div>
                    <div className="pop-up__container">
                        <p>{props.innerText}</p>

                        <a href="javascript:;" className="closer" onClick={(e) => props.handleCloser(e)}>X</a>
                    </div>

                </div>
            </div>
        </Fragment>
    )
}


