import React, {Component, Fragment} from 'react';
import Slider from 'react-slick';
import '../style/slider/slider.scss'


export default class SimpleSlider extends React.Component {
    render () {
        let settings = {
            centerMode: true,
            dots: false,
            infinite: true,
            speed: 500,
            touchThreshold: 10,
            slidesToShow: 3,
            slidesToScroll: 1,
            easing : 'ease-in-out'
        };
        return (
            <Slider {...settings}>

                <div><h3>1</h3></div>
                <div><h3>2</h3></div>
                <div><h3>3</h3></div>
                <div><h3>4</h3></div>
                <div><h3>5</h3></div>
                <div><h3>6</h3></div>
            </Slider>
        );
    }
}





